body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 30px;
}

@media screen and (max-width: 800px)
{
  p {
    font-size: 15px;
  }
}

@media screen and (max-width: 700px)
{
  p {
    font-size: 13px;
  }
}

@media screen and (max-width: 600px)
{
  p {
    font-size: 10px;
  }
}

.App footer {
  background-color: #AAD3C9;
  margin: 0;
}

.App footer p {
  margin: 0;
  padding: 1rem;
}
input {
  width: 25rem;
  height: 1.5rem;
  border-radius: 10px;
  border: none;
  padding: 0 20px;
  font-family: "Roboto Mono", monospace;
  background-color: rgba(255,255,255, 0.8);;
}
.OneReviewPreview {
  background-color: pink;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  width: 30%;
  border-radius: 5px;
  background-color: rgb(229, 236, 234);
  border: 2px solid #AAD3C9;
}

.OneReviewPreview > img {
  width: 30%;
}

.ReviewPreview {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 70%;
}

.RestaurantPicturePreview {
  width: 100%;
}

.RestaurantInfoOverlay {
  position: absolute;
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 98%;
  transition: 0.5s ease;
  background-color: rgba(213, 216, 220, 0.7);
  opacity: 0;
  display: block;
}

.RestaurantInfoOverlay > p {
  margin: 0;
}
.OneRestaurantPicturePreview:hover .RestaurantInfoOverlay {
  opacity: 1;
}

.OneRestaurantPicturePreview {
  position: relative;
  width: 22%;
  margin: 10px;
}

.OneRestaurantPicturePreview img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.ListofRestaurantPicturePreview {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  align-items: center;
  justify-content: center;
}


.HomePage {
  margin: 0;
}

.HomePageHeader {
  background-image: url("https://images.unsplash.com/photo-1543364195-bfe6e4932397?ixlib=rb-1.2.1&auto=format&fit=crop&w=1001&q=80");
  background-size: cover;
  height: 25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h2 {
  margin: 0 0 40px 0;
}

.TextUnderSearch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}

.UserCompanyInfo {
  margin: 0;
  padding: 0;
}

.UserCompanyInfo > p {
  margin: 0;
  font-size: 0.8rem;

}

.FilterLink {
  margin: 0;
}

.FilterLink > button {
  border: none;
  font-family: "Roboto Mono", monospace;
  background: transparent;
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
  text-decoration: underline;
}

.reviewsCaroussel {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.Signup > form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
label {
  display: block;
}
.AddReview > form {
  display: flex;
  flex-direction: column;
}
.HiddenAddReviewForm {
  display: none;
}
.NavBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  background-color: #AAD3C9;
  height: 5rem;
  margin: 0;
  padding-left: 10%;
  padding-right: 10%;
}

@media screen and (max-width: 950px)
{
  .NavBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    background-color: #AAD3C9;
    height: 5rem;
    margin: 0;
    padding-left: 5%;
    padding-right: 5%;
  }
}

