{

}

@media screen and (max-width: 600px)
{

}

.logo {
  height: 80%;
} 

.logo > img {
  max-height: 100%;
}

.LoggedinUserNav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.LoggedinUserNav > p {
  margin-right: 1rem;
}

.NavBar button {
  border: none;
  background-color: transparent;
  font-family: "Roboto Mono", monospace;
}

.NavBar a {
  text-decoration: none;
  color: black;
}

@media screen and (max-width: 800px)
{
  .NavBar button {
    font-size: 15px;
  }
  .LoggedinUserNav p {
    font-size: 15px;
  }
}

@media screen and (max-width: 600px)
{
  .NavBar button {
    font-size: 13px;
  }
  .LoggedinUserNav p {
    font-size: 13px;
  }

}

@media screen and (max-width: 550px)
{
  .NavBar button {
    font-size: 10px;
  }
  .LoggedinUserNav p{
    font-size: 10px;
  }
}

